@import "../../../shared/styles/mixin.scss";

.product_detail_container {
  @include responsive-layout;
  padding-top: 70px;

  nav {
    background-color: transparent !important;
    box-shadow: none !important;
    a {
      color: black !important;
      &::before {
        color: black !important;
      }
    }
  }

  .product_detail {
    max-height: 520px;
    display: grid;
    grid-template-columns: 38% 38% 24%;
    grid-template-rows: 50% 50%;
    grid-template-areas:
      "product_info product_view1 product_view2"
      "product_info product_view1 product_view3";
    grid-gap: 0.4em;
    margin: 1em 0 2em 0;

    .product_info {
      grid-area: product_info;
      .price {
        font-size: 1.2em;
        font-weight: bold;
      }
    }
    .product_view1 {
      grid-area: product_view1;
    }
    .product_view2 {
      grid-area: product_view2;
    }
    .product_view3 {
      grid-area: product_view3;
    }

    .product_view1,
    .product_view2,
    .product_view3 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      overflow: hidden;
      img {
        max-width: 100%;
      }
    }

    .product_info {
      padding-right: 4.5em;
      .main_data {
        display: grid;
        margin: 1em 0;
        // grid-template-columns: 100px max-content;
      }

      .sizes {
        display: flex;
        align-items: center;
        margin: 1em 0;

        .sizes_container {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .sizes_list > .button_item {
          border: 1px solid;
          margin: 0 0.2em;
        }
      }

      .colors {
        display: flex;
        align-items: center;
        .colors_list {
          display: flex;
          .button_item {
            border: 1px solid;
            margin: 0 0.2em;
            div {
              width: 15px;
              height: 15px;
              background: green;
              margin: 0 0.2em;
              border-radius: 50%;
            }
          }
        }
      }
      .buttons {
        button {
          margin-right: 1em;
        }
      }
    }
  }

  .title_related_products {
    margin: 0.3em 0;
    display: flex;
    flex-direction: column;
    * {
      margin: 0;
    }
  }

  .related_products {
  }
}
